import React, {Component, PropTypes}  from "react";
import {render} from "react-dom";
class Title extends React.Component {
    constructor(props) {
        super(props);
        this.displayName = "Title";
        this.state = {};
    }
    render() {
        return (
            <div>
                {this.props.title}
            </div>
        )
    }
}
export default  Title


