import React from "react";
import {render} from "react-dom";
import {connect} from "react-redux"
class Article extends React.Component{
    constructor(){
        super();
        this.displayName="Article";
        this.state={
            a:"我是文章详情"
        }
    }
    render(){
        return (
            <div>
                <p>{  this.state.a}</p>
            </div>
        )
    }
}
export default  connect()(Article)


